document.addEventListener("DOMContentLoaded", ()=>{
    iniciarApp();
})

function iniciarApp(){
    navegacionFija();
    crearGaleria();
    scrollNav();
}

function navegacionFija(){
    const header = document.querySelector("header");
    const sobre_festival = document.querySelector(".sobre-festival");
    const body = document.querySelector("body");
    window.addEventListener("scroll", function(){
        if (sobre_festival.getBoundingClientRect().bottom < 0){
            header.classList.add("fijo");
            body.classList.add("body-scroll");
        }
        else{
            header.classList.remove("fijo");
            body.classList.remove("body-scroll");
        }
    });
}

function crearGaleria(){
    const galeria = document.querySelector(".galeria-imagenes");

    for(let i = 1; i<=12; i++){
        const imagen = document.createElement("picture");

        imagen.innerHTML = `
        <source srcset="build/img/thumb/${i}.avif" type="image/avif">
        <source srcset="build/img/thumb/${i}.webp" type="image/webp">

        <img loading="lazy" width="200" height="300" src="build/img/thumb/${i}.jpg" alt="imagen de galeria">
        `;

        imagen.onclick = function(){
            mostrarImagen(i);
        };

        galeria.appendChild(imagen)
    }
}

function scrollNav(){
    const enlaces = document.querySelectorAll(".navegacion-principal a"); //selecciona todas las etiquetas
    enlaces.forEach(enlace =>{ //por cada etiqueta le agregar치 lo siguiente
        enlace.addEventListener("click", function(e){ //si se hace click en la etiqueta
            e.preventDefault(); //quita su evento por default

            const href = enlace.getAttribute("href"); //obtiene su atributo href (su contenido)
            const seccion = document.querySelector(href); //selecciona ese atributo que tiene
            seccion.scrollIntoView({behavior: "smooth"}); //le agrega el comportamiento smooth
        });
    });
}

function mostrarImagen(id){
    const imagen = document.createElement("picture");

    //crea la imagen
    imagen.innerHTML = `
    <source srcset="build/img/grande/${id}.avif" type="image/avif">
    <source srcset="build/img/grande/${id}.webp" type="image/webp">
    
    <img loading="lazy" width="200" height="300" src="build/img/grande/${id}.jpg" alt="imagen de galeria">
    `;
    

    //crea el overlay con la imagen
    const overlay = document.createElement("nav");
    overlay.classList.add("overlay");
    overlay.onclick = function(){
        //* esta funci칩n va a eliminar la imagen grande
        overlay.remove();
        //* activamos el scroll
        const body = document.querySelector("body");
        body.classList.remove("fijar-body");
    }
    overlay.appendChild(imagen);

    //boton para el modal
    const cerrarFoto = document.createElement("P");    
    cerrarFoto.textContent = 'X';
    cerrarFoto.classList.add("btn-cerrar");
    cerrarFoto.onclick = function(){
        //* esta funci칩n va a eliminar la imagen grande
        overlay.remove();
        //* activamos el scroll
        const body = document.querySelector("body");
        body.classList.remove("fijar-body");
    }
    overlay.appendChild(cerrarFoto);

    //a침ade el overlay con la imagen al html
    const body = document.querySelector("body");
    body.appendChild(overlay);
    body.classList.add("fijar-body");
}